@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1210 {
  @media (max-width: $vp-1210) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: $vp-1023) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-767) {
    @content;
  }
}

@mixin vp-320 {
  @media (max-width: $vp-320) {
    @content;
  }
}

